export default {
	
	data() {
		return {
			tabCurrentIndex: 0,
			addClass:'none',
			showList:[],//商品分类列表
			loadingType: 'more', //more 是加载中   nomore是加载结束
			total:0,//总条数
			pageSize:0,//每页条数
			page:1,//当前页
			nextPage:false,//是否有下一页
			tabList: [{
					state: 0,
					text: '新建分类',
				}
			],
			tabIndex:null,
			parentId:null,//上级分类id
			cateName:null,//分类名
			classification:'',
			
		};
	},
	
	async onLoad(){
		let userInfo=uni.getStorageSync('userInfo');
		
		if(userInfo!==undefined){
			this.token = userInfo.token;
			this.status = 10;
			this.loadData();
		}else{
			uni.navigateTo({
				url:'/pages/login/login'
			})
		}
		
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		// this.loadData();
	},
	
	methods: {
		//获取商品分类信息
		loadData(){
			let self = this;
			uni.request({
				url: 'http://wxshop.qclike.cn/api/admins/showlist', 
				method:'POST',
				data:{
					token:self.token,
					page:self.page
				},
				success: function(res) {
					if(res.data.code == 1){
						let result = res.data.data;
						if(res.data.code == 1){
							self.showList = result.categorydata;
							
						}
					}
					
				}
			});
			
		}, 
		//禁止手指滑动
		stopTouchMove: function() {
		  return false;
		},

		
		/* selectItem(todo){
			console.log(todo);
			let classifyId = todo.id;
			let classifyName = todo.name;
			uni.navigateTo({
				url:`/pages/goods/addgoods?classifyId=${classifyId}&classifyName=${classifyName}`
			})
		}, */
		//确认新增分类或修改分类
		tabConfirm(){
			let self = this;
			if(!self.classification){ 
				uni.showToast({title: '请输入分类名称',icon:"none"});
				return ; 
			}
			self.specClass= 'show';
			self.addClass = 'none';
			if(self.tabIndex == 0){//新增分类
				uni.request({
					url: 'https://wxshop.qclike.cn/api/admins/cateupdate',
					method:'POST',
					header:{
						'content-type': 'application/x-www-form-urlencoded', 
					},
					data:{
							token:self.token,
							parent_id:0,
							cate_name:self.classification
							
						},
						success: function(res) {
							if(res.data.code == 1){
								
								self.classification = '';
								
								self.$nextTick(function(){
									self.loadData();
								})
								
							} else if(res.data.code == 0){
								let msg = res.data.msg
								uni.showToast({title: msg,icon:"none"});
							}
					}
				});	
			}
			if(self.tabIndex == 6){//修改分类
			console.log('修改分类',self.classification)
				uni.request({
					url: 'https://wxshop.qclike.cn/api/admins/cateupdate',
					method:'POST',
					header:{
						'content-type': 'application/x-www-form-urlencoded', 
					},
					data:{
							token:self.token,
							parent_id:self.parentId,
							cate_name:self.classification,
							
						},
						success: function(res) {
							if(res.data.code == 1){
								self.$nextTick(function(){
									self.loadData();
								})
								
							} else if(res.data.code == 0){
								let msg = res.data.msg
								uni.showToast({title: msg,icon:"none"});
							}
					}
				});
			}
				
		},
		
		//取消新增分类
		tabCancel(){
			console.log('取消新增分类')
			this.specClass= 'show';
			
			this.addClass = 'none';
		},
		toggleAddClass(){
			if(this.addClass === 'show'){
				this.addClass = 'hide';
				setTimeout(() => {
					this.addClass = 'none';
				}, 250);
			}else if(this.addClass === 'none'){
				this.addClass = 'show';
			}
		},
		//底部tab切换
		tabClick(index,todo){
			let self = this;
			self.tabIndex = index;
			if(self.addClass === 'show'){
				self.addClass = 'hide';
				setTimeout(() => {
					self.addClass = 'none';
				}, 250);
			}else if(self.addClass === 'none'){
				self.addClass = 'show';
			}
			if(todo){
				self.parentId = todo.id;
			    self.cateName = todo.name;
				self.classification = todo.name;
			}
		},
		
		//删除操作
		deletes(todo){
			let self = this;
			uni.showModal({
				title: '删除提示',
				content: '你将删除这个分类',
				success: (res)=>{
					if (res.confirm) {
						uni.request({
							url: 'https://wxshop.qclike.cn/api/admins/catedel',
							method:'POST',
							header:{
								'content-type': 'application/x-www-form-urlencoded', 
							},
							data:{
									token:self.token,
									ids:todo.id
								},
								success: function(res) {
									if(res.data.code == 1){
										self.$nextTick(function(){
											self.loadData();
										})
										
									} else if(res.data.code == 0){
										let msg = res.data.msg
										uni.showToast({title: msg,icon:"none"});
									}
							}
						});
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
				
		},
		
		
		
	}
}
